import {Component, forwardRef, Input} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {FieldBaseComponent} from '../../../base/field-base.component';

declare let IMask: any;

@Component({
  selector: 'tamin-field-number',
  templateUrl: './tamin-field-number.component.html',
  styleUrls: ['./tamin-field-number.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldNumberComponent),
      multi: true
    }
  ]
})
export class TaminFieldNumberComponent extends FieldBaseComponent {
  @Input() min = Number.MIN_SAFE_INTEGER;
  @Input() max = Number.MAX_SAFE_INTEGER;
  @Input() negativeNumberColor = 'red';

  initializeComponent() {
    const me = this;
    this.maskRef = new IMask(this.inputElement.nativeElement, {
      mask: Number,
      thousandsSeparator: ',',
      min: me.min,
      max: me.max,
      radix: '.',
      scale: 2,
    });
  }
}
