import {TaminActionColumn} from '../../models/tamin-action-column.model';
import {TaminDataColumn} from '../../models/tamin-data-column.model';
import {SearchParam} from '../../models/search-param.model';
import {SortParam} from '../../models/sort-param.model';
import {TaminDataGridConfiguration} from './tamin.data.grid.configuration';

export class TaminDataGridConfigurationFactory {
  private _data = new TaminDataGridConfiguration();

  public constructor() {
  }

  public addUrl(url: string): this {
    this._data.serviceUrl = url;
    return this;
  }

  public addVisibleColumn(column: TaminDataColumn | object): this {
    this._data.visibleColumns.push(<TaminDataColumn>column);
    return this;
  }

  public clearVisibleColumns(): this {
    this._data.visibleColumns = [];
    return this;
  }

  public addActionColumn(column: TaminActionColumn | object): this {
    this._data.actionColumns.push(<TaminActionColumn>column);
    return this;
  }

  public clearActionColumns(): this {
    this._data.actionColumns = [];
    return this;
  }

  public addSearchParam(param: SearchParam | object): this {
    this._data.searchParams.push(<SearchParam>param);
    return this;
  }

  public clearSearchParams(): this {
    this._data.searchParams = [];
    return this;
  }

  public addSortParam(param: SortParam | object): this {
    this._data.sortParams.push(<SortParam>param);
    return this;
  }

  public clearSortParams(): this {
    this._data.sortParams = [];
    return this;
  }

  public setViewType(viewType: string): this {
    this._data.viewType = viewType;
    return this;
  }

  public setShowActionColumn(val: boolean): this {
    this._data.showActionColumn = val;
    return this;
  }

  public setActionColumnCaption(caption: string): this {
    this._data.actionColumnCaption = caption;
    return this;
  }

  public setId(id: string): this {
    this._data.id = id;
    return this;
  }

  public setRowEditable(val: boolean): this {
    this._data.rowEditable = val.toString();
    return this;
  }

  public setRowDeletable(val: boolean): this {
    this._data.rowDeletable = val.toString();
    return this;
  }

  public setDataItems(items: any): this {
    this._data.dataItems = items;
    return this;
  }

  public setShowFooter(val: boolean): this {
    this._data.showFooter = val.toString();
    return this;
  }

  public setShowPager(val: boolean): this {
    this._data.showPager = val;
    return this;
  }

  public setPagerSize(val: number): this {
    this._data.pagerSize = val;
    return this;
  }

  public setFirstLoad(val: boolean): this {
    this._data.loadDataOnInit = val;
    return this;
  }

  public setPagerCurrentPage(val: number): this {
    this._data.pagerCurrentPage = val;
    return this;
  }

  public getData(): TaminDataGridConfiguration {
    return this._data;
  }
}
