import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';

@Component({
  selector: 'tamin-wizard-item',
  templateUrl: './tamin-wizard-item.component.html',
  styleUrls: ['./tamin-wizard-item.component.scss']
})
export class TaminWizardItemComponent implements OnInit {
  @Input() stepTitle: string;
  @Input() buttonTitle: string;
  @ViewChild('wizardItem') wizardItem: ElementRef;

  private _visible: boolean;

  get visible(): boolean {
    return this._visible;
  }

  constructor() {
  }

  ngOnInit() {
    this.hide();
  }

  show() {
    this.wizardItem.nativeElement.style.display = 'block';
    this._visible = true;
  }

  hide() {
    this.wizardItem.nativeElement.style.display = 'none';
    this._visible = false;
  }
}
