import {AfterViewInit, ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {PickerContainerComponent} from '../../containers/picker-container/picker-container.component';

@Component({
  selector: 'tamin-data-grid-action-menu',
  templateUrl: './tamin-data-grid-action-menu.component.html',
  styleUrls: ['./tamin-data-grid-action-menu.component.scss'],
})
export class TaminDataGridActionMenuComponent implements OnInit, AfterViewInit {

  @ViewChild('dropdown') dropdown: ElementRef;
  @ViewChild('picker') picker: PickerContainerComponent;
  @Input() actionColumns = [];
  @Input() dataItem: any;
  @Output() menuItemClicked = new EventEmitter<any>();
  @Output() dropdownClicked = new EventEmitter<any>();


  constructor() {
  }

  ngOnInit() {
    // this.picker.parent = this.dropdown;
    // this.picker.parent = new ElementRef(this.dropdown.nativeElement.parentNode);
  }

  theMenuItemClicked(actionColumn: any) {
    this.picker.toggle();
    const item = this.dataItem;
    this.menuItemClicked.emit({actionColumn, item});
  }

  dropdownClickedHandler() {
    this.picker.toggle();
    this.dropdownClicked.emit();
  }

  ngAfterViewInit(): void {
    this.picker.parent = new ElementRef(this.dropdown.nativeElement.parentNode);
  }
}
