import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {timeout} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {TaminModalComponent} from '../tamin-modal/tamin-modal.component';

@Component({
  selector: 'tamin-pdf-viewer',
  templateUrl: './tamin-pdf-viewer.component.html',
  styleUrls: ['./tamin-pdf-viewer.component.scss']
})
export class TaminPdfViewerComponent implements OnInit {

  @ViewChild('theModal') theModal: TaminModalComponent;
  @ViewChild('iframe') iframe: ElementRef;
  @Input() title: string;
  @Input() errorMessage = 'Ø®Ø·Ø§ Ø¯Ø± Ø¯Ø±ÛØ§ÙØª Ø§Ø·ÙØ§Ø¹Ø§Øª';
  @Output() beforeLoad = new EventEmitter<any>();
  @Output() afterLoad = new EventEmitter<any>();

  private _overlay: any;

  constructor(private http: HttpClient) {
  }

  getBlob(url: string): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.http.get(url, {responseType: 'blob'})
        .pipe(timeout(6000))
        .toPromise()
        .then((response) => resolve(response))
        .catch((error) => reject(error));
    });
  }

  open(url: string) {
    this.beforeLoad.emit();
    this.getBlob(url)
      .then(value => {
        this.iframe.nativeElement.src = URL.createObjectURL(value);
        this.afterLoad.emit();
        this.theModal.show();
      })
      .catch(reason => {
        this.afterLoad.emit();
      });
  }

  close() {
    this.theModal.hide();
  }

  ngOnInit() {
  }
}
