import {Component, ContentChildren, forwardRef, Input, OnInit, QueryList} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';
import {TaminFieldRadioButtonComponent} from '../tamin-field-radio-button/tamin-field-radio-button.component';

@Component({
  selector: 'tamin-field-radio-button-group',
  templateUrl: './tamin-field-radio-button-group.component.html',
  styleUrls: ['./tamin-field-radio-button-group.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldRadioButtonGroupComponent),
      multi: true
    }
  ]
})
export class TaminFieldRadioButtonGroupComponent implements OnInit, ControlValueAccessor {
  @ContentChildren(TaminFieldRadioButtonComponent)
  radioButtons: QueryList<TaminFieldRadioButtonComponent>;
  onChange: any;
  onTouched: any;
  @Input() public formControlName: string;

  constructor() {
    this.onTouched = () => {
    };
    this.onChange = (value: string) => {
    };
  }

  ngOnInit(): void {
    setTimeout(() => {
      this.radioButtons.forEach(item => {
        item.name = this.formControlName;
        item.change.subscribe(value => {
          this.onChange(value);
        });
      });
    });
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  writeValue(obj: any): void {
    setTimeout(() => {
      this.radioButtons.forEach(item => {
        if (item.value === obj) {
          item.setSelected();
        }
      });
    });
  }
}
