import {AfterContentInit, Component, ContentChildren, OnDestroy, OnInit, QueryList} from '@angular/core';
import {TaminAccordionComponent} from '../tamin-accordion/tamin-accordion.component';

@Component({
  selector: 'tamin-accordion-group',
  templateUrl: './tamin-accordion-group.component.html',
  styleUrls: ['./tamin-accordion-group.component.scss']
})
export class TaminAccordionGroupComponent implements OnInit, OnDestroy, AfterContentInit {

  @ContentChildren(TaminAccordionComponent) accordions: QueryList<TaminAccordionComponent>;
  private subscriptions = [];

  private _accordions;

  constructor() {
  }

  ngOnInit() {
  }

  ngAfterContentInit() {

    this._accordions = this.accordions;
    this.removeSubscriptions();
    this.addSubscriptions();

    this.accordions.changes.subscribe(rex => {
      this._accordions = rex;
      this.removeSubscriptions();
      this.addSubscriptions();
    });
  }

  addSubscriptions() {
    this._accordions.forEach(a => {
      const subscription = a.toggleAccordion.subscribe(e => {
        this.toogleAccordion(a);
      });
      this.subscriptions.push(subscription);
    });
  }

  removeSubscriptions() {
    this.subscriptions.forEach(sub => {
      sub.unsubscribe();
    });
  }

  toogleAccordion(accordion) {
    if (!accordion.active) {
      this.accordions.forEach(a => a.active = false);
    }
    // set active accordion
    accordion.active = !accordion.active;
  }

  ngOnDestroy() {
    this.removeSubscriptions();
  }

}
