import {Component, EventEmitter, OnInit} from '@angular/core';
import {Router} from '@angular/router';

@Component({
  selector: 'tamin-site-bottom-menu',
  templateUrl: './tamin-site-bottom-menu.component.html',
  styleUrls: ['./tamin-site-bottom-menu.component.scss']
})
export class TaminSiteBottomMenuComponent implements OnInit {

  constructor(private router: Router) {
  }

  ngOnInit() {
  }

  handleMenu(item) {
    if (item.url !== '') {
        this.router.navigate([item.url]);
    }
  }
}
