import {Component, EventEmitter, forwardRef, Input, OnInit, Output} from '@angular/core';
import {FieldBaseComponent} from '../../../base/field-base.component';
import {NG_VALUE_ACCESSOR} from '@angular/forms';

@Component({
  selector: 'tamin-field-editable',
  templateUrl: './tamin-field-editable.component.html',
  styleUrls: ['./tamin-field-editable.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldEditableComponent),
      multi: true
    }
  ]
})
export class TaminFieldEditableComponent extends FieldBaseComponent {

  @Input() maxLength = Number.MAX_SAFE_INTEGER;
  @Output() focus = new EventEmitter();
  @Output() blur = new EventEmitter();
  @Output() edit = new EventEmitter();

}
