import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'tamin-accordion',
  templateUrl: './tamin-accordion.component.html',
  styleUrls: ['./tamin-accordion.component.scss']
})
export class TaminAccordionComponent implements OnInit {

  @Input() title: string;
  @Input() active = false;
  @Output() toggleAccordion: EventEmitter<boolean> = new EventEmitter();

  constructor() {}

  onClick(event) {
    event.preventDefault();
    this.toggleAccordion.emit(this.active);
  }

  ngOnInit() {
  }

}
