import {
  AfterContentInit,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  Output,
  ViewChild
} from '@angular/core';
import {
  AbstractControl,
  ControlContainer,
  ControlValueAccessor,
  NG_VALUE_ACCESSOR
} from '@angular/forms';
import {ITaminField} from '../ITaminField';

declare let IMask: any;

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'tmn-field-num',
  template: `<input
    type="text"
    [(ngModel)]="value"
    (ngModelChange)="updateChanges()"
    class="tamin-form-control-input"
    style="direction: ltr"
    (blur)="onTouched()"
    [disabled]="control?.disabled"
    [class.tamin-form-control-valid]="control?.touched && control?.valid"
    [class.tamin-form-control-invalid]="control?.touched && !control?.valid"
    [style.color]="value < 0 ? negativeNumberColor : ''"
    [attr.placeholder]="placeHolder"
    #element>`,
  styles: [
    `:host {
      width: 100%;
    }`
  ],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => FieldNumComponent),
    multi: true
  }]
})
export class FieldNumComponent implements ControlValueAccessor, ITaminField, AfterContentInit {
  control: AbstractControl;
  maskRef: any;
  @ViewChild('element') element: ElementRef;
  @Input() maxLength = Number.MAX_SAFE_INTEGER;
  @Input() formControlName: string;
  @Input() placeHolder: string;
  @Input() min = Number.MIN_SAFE_INTEGER;
  @Input() max = Number.MAX_SAFE_INTEGER;
  @Input() negativeNumberColor = 'red';
  @Output() blur = new EventEmitter();

  set value(val: any) {
    this.maskRef.value = val.toString();
  }

  get value() {
    return this.maskRef.value;
  }

  onChange: (_: any) => void = (_: any) => {
  }

  onTouched: () => void = () => {
    this.blur.emit();
  }

  constructor(private controlContainer: ControlContainer) {

  }

  updateChanges() {
    this.onChange(this.value);
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  writeValue(obj: any): void {
    this.value = obj;
    this.updateChanges();
  }

  ngAfterContentInit(): void {
    this.control = this.controlContainer.control.get(this.formControlName);
    const me = this;
    this.maskRef = new IMask(this.element.nativeElement, {
      mask: Number,
      thousandsSeparator: ',',
      min: me.min,
      max: me.max
    });
  }
}
