import {ChangeDetectorRef, Component, ElementRef, EventEmitter, HostListener, Input, OnInit, Output, ViewChild} from '@angular/core';
import PopperJs, {Placement} from 'popper.js';

@Component({
  selector: 'tamin-picker-container',
  templateUrl: './picker-container.component.html',
  styleUrls: ['./picker-container.component.scss']
})
export class PickerContainerComponent implements OnInit {

  @Input() shown: boolean;
  @Input() parent: ElementRef;
  @Input() button: ElementRef;
  @Input() adjustWidth = true;
  @Input() placement = 'bottom-end';
  @Input() padding = '5px';
  @Output() berforeHide = new EventEmitter<void>();
  @Output() afterHide = new EventEmitter<void>();
  @Output() berforeShow = new EventEmitter<void>();
  @Output() afterShow = new EventEmitter<void>();
  @ViewChild('picker') picker: ElementRef;
  private _thePopper: PopperJs;

  constructor(private changeDetectorRef: ChangeDetectorRef) {
  }

  toggle() {
    if (this.shown) {
      this.hide();
    } else {
      this.show();
    }
  }

  show() {
    this.berforeShow.emit();
    this.shown = true;
    this.changeDetectorRef.detectChanges();
    if (this.adjustWidth) {
      this.picker.nativeElement.style.width = this.parent.nativeElement.getBoundingClientRect().width - 3 + 'px';
    }
    if (this._thePopper) {
      this._thePopper = null;
    }
    this._thePopper = new PopperJs(this.parent.nativeElement, this.picker.nativeElement, {
      placement: <Placement>this.placement,
    });
    this.afterShow.emit();
  }

  hide() {
    this.berforeHide.emit();
    this.shown = false;
    this.afterHide.emit();
  }

  ngOnInit() {
  }

  update() {
    if (this.shown) {
      this._thePopper.scheduleUpdate();
    }
  }

  @HostListener('document:click', ['$event'])
  @HostListener('document:keyup', ['$event'])
  documentEventHandler(event) {
    if (this.shown) {
      if (event.code === 'Tab' || event.code === 'Escape') {
        this.toggle();
      }

      let shouldHidePicker = !this.picker.nativeElement.contains(event.target);
      shouldHidePicker = shouldHidePicker && !this.parent.nativeElement.contains(event.target);
      if (this.button) {
        shouldHidePicker = shouldHidePicker && !this.button.nativeElement.contains(event.target);
      }

      if (shouldHidePicker) {
        this.hide();
      }
    }
  }
}
