import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {TaminSecurityService} from '../../../services/tamin-security/tamin-security.service';

@Component({
  selector: 'tamin-site-header',
  templateUrl: './tamin-site-header.component.html',
  styleUrls: ['./tamin-site-header.component.scss']
})
export class TaminSiteHeaderComponent implements OnInit {
  userName: string;
  @Input() showLogin = true;
  loggedIn = false;
  @Output() showProfile = new EventEmitter<void>();

  constructor(
    private taminSecurityService: TaminSecurityService) {
  }

  ngOnInit() {
    if (!this.taminSecurityService.checkToken()) {
      this.loggedIn = false;
      this.userName = '';
    } else {
      this.loggedIn = true;
      // if (this.taminSecurityService.getCurrentUser()) {
      //   this.userName = `${this.taminSecurityService.currentUser.firstName} ${this.taminSecurityService.currentUser.lastName}`;
      // }
      this.taminSecurityService.getCurrentUser()
        .then(data => this.userName = `${data.firstName} ${data.lastName}`)
        .catch(error => {
          this.userName = ' ';
        });
    }
  }

  onLogin() {
    this.taminSecurityService.redirectToLogin();
  }

  onLogout() {
    this.taminSecurityService.redirectToLogout();
  }

  onUserProfile() {
    this.showProfile.emit();
  }
}
