import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { SearchParam } from '../../models/search-param.model';
import { SortParam } from '../../models/sort-param.model';
import { EntityResultWrapper } from '../../models/entity-result-wrapper.model';
import { ObjectResultWrapper } from '../../models/object-result-wrapper.model';
// import { Observable } from "rxjs";
import { timeout } from 'rxjs/operators';

@Injectable()
export class GenericRestService<T> {
  public restUrl = '';
  public timeout = 180000;
  private pagingPath = '';

  constructor(private http: HttpClient) {
  }

  getAll(querySearchParams: SearchParam[], querySortParams: SortParam[]): Promise<ObjectResultWrapper> {
    const headers = new HttpHeaders();
    let params = new HttpParams();
    params.append('filter', JSON.stringify(querySearchParams));
    params.append('sort', JSON.stringify(querySortParams));
    params = params.append('_dc', (new Date()).getTime().toString());
    return new Promise<ObjectResultWrapper>((resolve, reject) => {
      this.http.get<ObjectResultWrapper>(this.restUrl, { headers, params })
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: ObjectResultWrapper) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  getPagedEntity(includeMetaData: boolean, pageNo: number, pageSize: number, querySearchParams: SearchParam[],
    querySortParams: SortParam[]): Promise<EntityResultWrapper<T>> {
    let params = new HttpParams();
    const headers = new HttpHeaders();
    params = params.append('filter', JSON.stringify(querySearchParams));
    params = params.append('sort', JSON.stringify(querySortParams));
    //params = params.append('start', pageNo.toString());
    params = params.append('start', ((pageNo - 1) * pageSize).toString());
    params = params.append('limit', pageSize.toString());
    params = params.append('_dc', (new Date()).getTime().toString());
    const url = `${this.restUrl}/${this.pagingPath}`;
    return new Promise<EntityResultWrapper<T>>((resolve, reject) => {
      this.http.get<EntityResultWrapper<T>>(url, { headers, params })
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: EntityResultWrapper<T>) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  getPagedObject(includeMetaData: boolean, pageNo: number, pageSize: number, querySearchParams: SearchParam[],
    querySortParams: SortParam[]): Promise<ObjectResultWrapper> {
    let params = new HttpParams();
    const headers = new HttpHeaders();
    params = params.append('filter', JSON.stringify(querySearchParams));
    params = params.append('sort', JSON.stringify(querySortParams));
    // params = params.append('start', pageNo.toString());
    params = params.append('start', ((pageNo - 1) * pageSize).toString());
    params = params.append('limit', pageSize.toString());
    params = params.append('_dc', (new Date()).getTime().toString());
    const url = `${this.restUrl}/${this.pagingPath}`;
    return new Promise<ObjectResultWrapper>((resolve, reject) => {
      this.http.get<ObjectResultWrapper>(url, { headers, params })
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: ObjectResultWrapper) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  getById(id: number): Promise<ObjectResultWrapper> {
    const url = `${this.restUrl}/${id}`;
    return new Promise<ObjectResultWrapper>((resolve, reject) => {
      this.http.get<ObjectResultWrapper>(url)
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: ObjectResultWrapper) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  getByMultipleId(ids: string[]): Promise<ObjectResultWrapper> {
    let url = `${this.restUrl}`;
    ids.forEach(id => {
      url += `/${id}`;
    });
    return new Promise<ObjectResultWrapper>((resolve, reject) => {
      this.http.get<ObjectResultWrapper>(url)
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: ObjectResultWrapper) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  create(dataItem: T): Promise<T> {
    const url = this.restUrl;
    return new Promise<T>((resolve, reject) => {
      this.http
        .post(url, dataItem)
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: T) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  update(dataItem: T): Promise<T> {
    const url = this.restUrl;
    return new Promise<T>((resolve, reject) => {
      this.http
        .put(url, dataItem)
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: T) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  delete(id: number): Promise<void> {
    const url = `${this.restUrl}/${id}`;
    return new Promise<void>((resolve, reject) => {
      this.http
        .delete(url)
        .pipe(timeout(this.timeout))
        .toPromise()
        .then((response: any) => resolve(response))
        .catch((error: Response) => reject(this.handleError(error)));
    });
  }

  protected handleError(error: any): Promise<any> {
    return error;
  }
}
