import {AfterViewInit, ChangeDetectorRef, Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {TaminLazyLoadService} from '../../services/tamin-lazy-load/tamin-lazy-load.service';
import {Subscription} from 'rxjs';
import {TaminModalComponent} from '../tamin-modal/tamin-modal.component';

declare let Cropper: any;

@Component({
  selector: 'tamin-image-editor',
  templateUrl: './image-editor.component.html',
  styleUrls: ['./image-editor.component.scss']
})
export class ImageEditorComponent implements OnInit, AfterViewInit, OnDestroy {

  private _subscription = new Subscription();
  @ViewChild('image') image: ElementRef;
  @ViewChild('thModal') thModal: TaminModalComponent;
  @ViewChild('imagePreview') imagePreview: ElementRef;
  cropper: any;

  constructor(
    private taminLazyLoadService: TaminLazyLoadService,
    private changeDetectorRef: ChangeDetectorRef) {
  }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
  }

  getImage(): any {
    this.cropper.getCroppedCanvas().toBlob((blob) => {
      const size = Math.ceil(blob.size / 1024).toString() + ' KB';
      alert(size);
    });
  }

  open(image) {
    const me = this;

    this._subscription.add(this.taminLazyLoadService.loadJs('assets/cropperjs/cropper.js').subscribe(value => {
        this._subscription.add(
          this.taminLazyLoadService.loadCss('assets/cropperjs/cropper.css').subscribe(value1 => {
            this.thModal.show();
            if (this.cropper) {
              this.cropper.clear();
              this.cropper.replace(image);
            } else {
              this.image.nativeElement.src = image;
              this.cropper = new Cropper(this.image.nativeElement, {
                aspectRatio: 16 / 9,
                preview: me.imagePreview.nativeElement,
                crop: function (e) {
                },
              });
            }
          })
        );
      })
    );
  }

  ngOnDestroy(): void {
    this._subscription.unsubscribe();
  }

}
