import {Component, ElementRef, Input, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {TaminLazyLoadService} from '../../../services/tamin-lazy-load/tamin-lazy-load.service';
import {Subscription} from 'rxjs';

declare let Viewer: any;

@Component({
  selector: 'tamin-image-viewer',
  templateUrl: './image-viewer.component.html',
  styleUrls: ['./image-viewer.component.css']
})
export class ImageViewerComponent implements OnInit, OnDestroy {

  @ViewChild('img') img: ElementRef;
  @Input() width = '100%';
  @Input() height = '300px';

  private subscription = new Subscription();

  constructor(private taminLazyLoadService: TaminLazyLoadService) {
  }

  ngOnInit() {
  }

  open(url: string) {
    this.subscription.add(this.taminLazyLoadService.loadJs('assets/viewerjs/dist/viewer.js').subscribe(value => {
        this.subscription.add(
          this.taminLazyLoadService.loadCss('assets/viewerjs/dist/viewer.css').subscribe(value1 => {
            this.img.nativeElement.src = url;
            const viewer = new Viewer(this.img.nativeElement, {
              inline: false,
              viewed() {
                viewer.zoomTo(1);
              },
            });
          })
        );
      })
    );
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
