import {ChangeDetectorRef, Component, ElementRef, HostListener, Input, OnInit, ViewChild} from '@angular/core';
import {TaminMenuModel} from '../../../models/tamin-menu.model';
import {GenericRestService} from '../../../services/generic-rest-service/generic-rest.service';
import {Router} from '@angular/router';
import {TaminRestService} from '../../../services/tamin-rest-service/tamin-rest.service';

declare var alertify: any;

@Component({
  selector: 'tamin-site-top-menu',
  templateUrl: './tamin-site-top-menu.component.html',
  styleUrls: ['./tamin-site-top-menu.component.scss']
})
export class TaminSiteTopMenuComponent implements OnInit {
  @ViewChild('desktopMenu') desktopMenu: ElementRef;
  @ViewChild('mobileMenu') mobileMenu: ElementRef;
  @ViewChild('topMenu') topMenu: ElementRef;
  @ViewChild('menuElement') menuElement: ElementRef;
  @ViewChild('mobileMenuBody') mobileMenuBody: ElementRef;
  @ViewChild('mobileMenuButton') mobileMenuButton: ElementRef;
  @Input() restUrl: string;
  public menuData: TaminMenuModel = new TaminMenuModel();
  stack: Array<TaminMenuModel> = [];
  dummy = true;

  constructor(private taminRestService: TaminRestService, private router: Router, private ref: ChangeDetectorRef) {
  }

  ngOnInit() {
    this.taminRestService.getAll(this.restUrl)
      .then(data => {
        this.menuData = data.data;
        this.stack.push(data.data);
      })
      .catch(error => {
      });
  }

  handleMenu(item: TaminMenuModel) {
    if (item.disable) {
      alertify.alert('Ù¾ÛØ§Ù Ø³ÛØ³ØªÙ', 'Ø§ÛÙ Ø®Ø¯ÙØª Ø¨Ø²ÙØ¯Û Ø¯Ø± Ø§Ø®ØªÛØ§Ø± Ú©Ø§Ø±Ø¨Ø±Ø§Ù ÙØ­ØªØ±Ù Ø³Ø§Ø²ÙØ§Ù ØªØ§ÙÛÙ Ø§Ø¬ØªÙØ§Ø¹Û ÙØ±Ø§Ø± Ø®ÙØ§ÙØ¯ Ú¯Ø±ÙØª.');
      return;
    }
    if (item.url !== '') {
      if (item['url-type'] === 'external') {
        window.open(item.url);
      }
      this.router.navigateByUrl('/' + item.url);
      this.dummy = false;
      this.ref.detectChanges();
      this.dummy = true;
      this.ref.detectChanges();
    }
  }

  toggleMobileMenu() {
    if (this.mobileMenuBody.nativeElement.style.display === 'block') {
      this.mobileMenuBody.nativeElement.style.display = 'none';
      this.mobileMenuButton.nativeElement.style.background = '#000a';
      this.mobileMenuButton.nativeElement.style.color = 'white';
    } else {
      this.mobileMenuBody.nativeElement.style.display = 'block';
      const rect = window.document.body.getBoundingClientRect();
      this.mobileMenuBody.nativeElement.style.width = rect.width + 'px';
      this.mobileMenuButton.nativeElement.style.background = '#00779caa';
      this.mobileMenuButton.nativeElement.style.color = 'white';
    }
  }

  @HostListener('window:resize', ['$event'])
  windowResizeHandler(event) {
    const rect = window.document.body.getBoundingClientRect();
    this.mobileMenuBody.nativeElement.style.width = rect.width + 'px';
  }

  onMobileMenuItemClick(menuItem) {
    if (menuItem.items !== undefined && menuItem.items.length !== 0) {
      this.stack.push(menuItem);
    }
  }

  onMobileMenuReturn() {
    this.stack.pop();
  }
}
