import {Component, EventEmitter, Input, OnDestroy, OnInit, Output} from '@angular/core';
import {GenericRestService} from '../../../services/generic-rest-service/generic-rest.service';
import {Subscription, timer} from 'rxjs';

@Component({
  selector: 'tamin-monitoring',
  templateUrl: './monitoring.component.html',
  styleUrls: ['./monitoring.component.scss']
})
export class MonitoringComponent implements OnInit, OnDestroy {
  @Input() serviceUrls: string[];
  @Input() refreshInterval = 60000;
  @Input() id: string;
  @Input() type: 'service' | 'database';
  @Input() title: string;
  @Input() normalWaitingTime = 10000;
  @Input() warningWaitingTime = 30000;
  @Input() dangerWaitingTime = 50000;
  @Input() timeout = 60000;
  @Input() viewType: 'full' | 'compact';
  @Output() started = new EventEmitter<void>();
  @Output() stopped = new EventEmitter<void>();
  lastStatus: 'normal' | 'warning' | 'danger';
  alive = false;

  serviceTotalRowsCollected: Map<string, number> = new Map<string, number>();
  serviceWaitingTimes: Map<string, number> = new Map<string, number>();
  serviceStartTimes: Map<string, number> = new Map<string, number>();
  dbWaitingTimes: Map<string, number> = new Map<string, number>();
  dbStartTimes: Map<string, number> = new Map<string, number>();
  statusColor = '#bbbbbb';

  dbChartLabels: string[] = [];
  dbChartData: any[] = [];
  dbChartType = 'bar';
  dbChartOptions = {};

  serviceChartLabels: string[] = ['ResponseTime', 'TimeoutRange'];
  serviceChartData: any[] = [];
  serviceChartType = 'pie';

  private _subscription = new Subscription();


  constructor(private genericRestService: GenericRestService<any>) {
    this.dbChartOptions = {
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true
          }
        }]
      }
    };
  }

  startTimer() {
    this.alive = true;
    this._subscription = timer(1000, this.refreshInterval).subscribe(() => {
        this.refreshData();
      }
    );
    this.started.emit();
  }

  stopTimer() {
    if (this.alive) {
      this.alive = false;
      this._subscription.unsubscribe();
    }
    this.stopped.emit();
    this.statusColor = '#bbbbbb';
  }

  ngOnInit() {
    if (this.serviceUrls) {
      for (let index = 0; index < this.serviceUrls.length; index++) {
        this.serviceStartTimes.set(this.serviceUrls[index], 0);
        this.serviceWaitingTimes.set(this.serviceUrls[index], 0);
        this.serviceTotalRowsCollected.set(this.serviceUrls[index], 0);
        this.serviceChartData.push(
          {data: [0, this.timeout], label: this.serviceUrls[index]}
        );
      }
    } else {
      this.serviceChartData.push({data: [0, 0], label: ''});
    }

    this.dbChartData.push({data: [0, 0], label: ''});
    this.startTimer();
  }

  public refreshData() {
    this.serviceUrls.forEach(serviceUrl => {
      this.serviceStartTimes.set(serviceUrl, Date.now());
      this.genericRestService.restUrl = serviceUrl;
      this.genericRestService.timeout = this.timeout;
      this.genericRestService.getPagedObject(true, 0, 10, [], [])
        .then(objectResultWrapper => {
          this.serviceWaitingTimes.set(serviceUrl, Date.now() - this.serviceStartTimes.get(serviceUrl));
          if (this.serviceWaitingTimes.get(serviceUrl) <= this.normalWaitingTime) {
            this.statusColor = 'green';
          }
          if (this.serviceWaitingTimes.get(serviceUrl) > this.normalWaitingTime && this.serviceWaitingTimes.get(serviceUrl) <= this.warningWaitingTime) {
            this.statusColor = 'green';
          }
          if (this.serviceWaitingTimes.get(serviceUrl) > this.warningWaitingTime) {
            this.statusColor = '#ff9900';
          }
          if (this.serviceWaitingTimes.get(serviceUrl) > this.dangerWaitingTime) {
            this.statusColor = 'red';
          }
          this.refreshServiceChart();
        })
        .catch(error => {
          this.serviceWaitingTimes.set(serviceUrl, this.timeout);
          this.statusColor = 'red';
          this.refreshServiceChart();
        });
    });
  }

  refreshServiceChart() {
    this.serviceChartData = [];
    this.serviceUrls.forEach(serviceUrl => {
      let newValue = this.timeout - this.serviceWaitingTimes.get(serviceUrl);
      if (newValue < 0) {
        newValue = 0;
      }
      this.serviceChartData.push(
        {data: [this.serviceWaitingTimes.get(serviceUrl), newValue], label: serviceUrl}
      );
    });
  }

  ngOnDestroy() {
    this._subscription.unsubscribe();
  }

  serviceChartClicked(e: any): void {
  }

  serviceChartHovered(e: any): void {
  }

  dbChartClicked(e: any): void {
  }

  dbChartHovered(e: any): void {
  }
}
