import {Component, forwardRef} from '@angular/core';
import {FieldBaseComponent} from '../../../base/field-base.component';
import {NG_VALUE_ACCESSOR} from '@angular/forms';

declare let IMask: any;

@Component({
  selector: 'tamin-field-natioanl-id',
  templateUrl: './tamin-field-natioanl-id.component.html',
  styleUrls: ['./tamin-field-natioanl-id.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldNatioanlIdComponent),
      multi: true
    }
  ]

})
export class TaminFieldNatioanlIdComponent extends FieldBaseComponent {

  initializeComponent() {
    this._setInputMask();
  }

  private _setInputMask() {
    this.maskRef = new IMask(this.inputElement.nativeElement, {
      mask: '0000000000'
    });
  }
}
