import {Injectable} from '@angular/core';
import * as momentNs from 'jalali-moment';

const moment = momentNs;

@Injectable({
  providedIn: 'root'
})
export class TaminPersianService {

  private _numberRange = '[\u06F0-\u06F9]';
  private _charRange = ['[\u06A9\u06AF\u06C0\u06CC\u060C',
    '\u062A\u062B\u062C\u062D\u062E\u062F',
    '\u063A\u064A\u064B\u064C\u064D\u064E',
    '\u064F\u067E\u0670\u0686\u0698\u200C',
    '\u0621-\u0629\u0630-\u0639\u0641-\u0654]'].join('');
  private _rtlPunctuations = '(Ø|Ø|Â«|Â»|Ø|Ù¬)';
  private _ltrPunctuations = '(\\.|:|\\!|\\-|\\[|\\]|\\(|\\)|/)';

  private _number = new RegExp('^' + this._numberRange + '+$');
  private _letter = new RegExp('^' + this._charRange + '+$');
  private _punctuation = new RegExp('^' + this.combineRegExps(this._rtlPunctuations, this._ltrPunctuations) + '+$');
  private _text = new RegExp('^' + this.combineRegExps(this._numberRange, this._charRange, this._rtlPunctuations, this._ltrPunctuations, '\\s') + '+$'
  );
  private _rtl = new RegExp('^' + this.combineRegExps(this._charRange, this._numberRange, this._rtlPunctuations, '\\s') + '+$');


  constructor() {
  }

  public getPersianDate(value: Date): string | null {
    try {
      const tmp = moment.from(value.toLocaleDateString(), 'en');
      if (tmp.isValid()) {
        return tmp.locale('fa').format('YYYY/MM/DD');
      }
      return null;
    } catch (e) {
      return '';
    }
  }

  public getPersianDayOfWeek(value: Date): string | null {
    try {
      const dayOfWeeks = ['ÛÚ©Ø´ÙØ¨Ù', 'Ø¯ÙØ´ÙØ¨Ù', 'Ø³Ù Ø´ÙØ¨Ù', 'ÚÙØ§Ø±Ø´ÙØ¨Ù', 'Ù¾ÙØ¬ Ø´ÙØ¨Ù', 'Ø¬ÙØ¹Ù', 'Ø´ÙØ¨Ù'];
      const tmp = moment.from(value.toLocaleDateString(), 'en');
      if (tmp.isValid()) {
        return dayOfWeeks[tmp.locale('fa').day()];
      }
      return null;
    } catch (e) {
      return '';
    }
  }

  public getGregorianDate(value: string): Date | null {
    const tmp = moment.from(value, 'fa', 'YYYY/MM/DD');
    if (tmp.isValid()) {
      return tmp.toDate();
    }
    return null;
  }

  private combineRegExps(...params: any[]) {
    let combined = '(';
    for (let i = 0; i < arguments.length; i++) {
      combined += '(';
      if (i !== arguments.length - 1) {
        combined += arguments[i] + ')|';
      } else {
        combined += arguments[i] + ')';
      }
    }
    return combined + ')';
  }
}
