import {Component, Input, OnInit} from '@angular/core';
import {TaminLazyLoadService} from '../../services/tamin-lazy-load/tamin-lazy-load.service';

declare var OpenLayers: any;

@Component({
  selector: 'tamin-map',
  templateUrl: './tamin-map.component.html',
  styleUrls: ['./tamin-map.component.scss']
})
export class TaminMapComponent implements OnInit {
  @Input() js = 'http://www.openlayers.org/api/OpenLayers.js';
  @Input() css: string;
  @Input() width = '100%';
  @Input() height = '100%';
  id: any;

  constructor(private taminLazyLoadService: TaminLazyLoadService) {
  }

  ngOnInit() {
    this.id = (new Date()).getTime().toString();
  }

  load(): Promise<any> {
    return this.taminLazyLoadService.loadJs(this.js).toPromise();
  }

  setLocation(latitude: number, longitude: number, zoom: number) {
    const map = new OpenLayers.Map(this.id);
    map.addLayer(new OpenLayers.Layer.OSM());
    const lonLat = new OpenLayers.LonLat(longitude, latitude)
      .transform(
        new OpenLayers.Projection('EPSG:4326'),
        map.getProjectionObject()
      );
    const markers = new OpenLayers.Layer.Markers('Markers');


    markers.addMarker(new OpenLayers.Marker(lonLat));
    map.setCenter(lonLat, zoom);
  }
}
