import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'persianNumber'
})
export class PersianNumberPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if (!value) {
      return null;
    }
    let tmp = value.toString();
    if (args && args === 'cs') {
      const parts = value.toString().split('.');
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      tmp = parts.join('.');
    }
    return tmp.toString();
    // .replace(/0/g, 'Û°')
    // .replace(/1/g, 'Û±')
    // .replace(/2/g, 'Û²')
    // .replace(/3/g, 'Û³')
    // .replace(/4/g, 'Û´')
    // .replace(/5/g, 'Ûµ')
    // .replace(/6/g, 'Û¶')
    // .replace(/7/g, 'Û·')
    // .replace(/8/g, 'Û¸')
    // .replace(/9/g, 'Û¹');
  }
}
