import {
  Component,
  ElementRef, EventEmitter,
  forwardRef,
  Input,
  OnInit, Output,
  ViewChild
} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';

@Component({
  selector: 'tamin-field-radio-button',
  templateUrl: './tamin-field-radio-button.component.html',
  styleUrls: ['./tamin-field-radio-button.component.css'],
})
export class TaminFieldRadioButtonComponent implements OnInit {

  @ViewChild('inputElement') inputElement: ElementRef;
  @Input() value: string;
  @Output() change = new EventEmitter<string>();
  private _name: string;
  get name(): string {
    return this._name;
  }

  @Input() set name(value: string) {
    setTimeout(() => {
      this._name = value;
    });
  }

  ngOnInit(): void {
  }

  setSelected() {
    this.inputElement.nativeElement.checked = true;
  }



  onChangeRadio(event: Event) {
    if (this.inputElement.nativeElement.checked) {
      this.change.emit(this.value);
    }
  }
}
