import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import {Subscription} from 'rxjs';
import {TaminLazyLoadService} from '../../services/tamin-lazy-load/tamin-lazy-load.service';
import {OverlayService} from '../../services/overlay/overlay.service';
import {isNullOrUndefined} from 'util';
import {Guid} from '../../helpers/guid';
import {PersianNumberPipe} from '../../pipes/persian-number.pipe';

declare var alertify: any;
declare let Viewer: any;
declare let filechooser: any;

/* In Cordova, we need to install the https://github.com/caixiangsap/filechooser.git */

@Component({
  selector: 'tamin-image-gallery',
  templateUrl: './tamin-image-gallery.component.html',
  styleUrls: ['./tamin-image-gallery.component.scss']
})

export class TaminImageGalleryComponent implements OnInit, AfterViewInit, OnDestroy {

  @ViewChild('imageContainer') imageContainer: ElementRef;
  @ViewChild('imageInput') imageInput: ElementRef;
  @ViewChild('host') host: ElementRef;
  images: Array<ImageModel> = [];
  private _gallery: any;
  @Input() insertable = true;
  @Input() maxImageSize = 120;
  @Output() afterInsert = new EventEmitter<ImageModel>();
  @Output() afterRemove = new EventEmitter<ImageModel>();
  @Output() beforeInsert = new EventEmitter();
  @Output() beforeRemove = new EventEmitter<ImageModel>();
  private _subscription = new Subscription();
  private _overlay: any;
  private defaultTitle: string;
  private defaultTag: any;

  constructor(private taminLazyLoadService: TaminLazyLoadService, private changeDetectorRef: ChangeDetectorRef, private overlayService: OverlayService) {
  }

  ngOnInit() {
  }

  selectImage(title, tag: any = null) {
    const me = this;
    this.defaultTitle = title;
    this.defaultTag = tag;
    if (window.hasOwnProperty('cordova')) {
      this.checkPermission().then(value => {
        if (value) {
          filechooser.open({'mime': 'image/*'},
            (data) => {
              me.getBase64FromImageUrl(data.url);
            },
            (reason) => {
              me.showAlert('Ù¾ÛØ§Ù Ø³ÛØ³ØªÙ', 'Ø®Ø·Ø§ Ø¯Ø± Ø¨Ø§Ø±Ú¯Ø°Ø§Ø±Û ÙØ§ÛÙ');
            });
        }
      }).catch(reason => {
        me.showAlert('Ù¾ÛØ§Ù Ø³ÛØ³ØªÙ', 'Ø®Ø·Ø§ Ø¯Ø± Ø¯Ø³ØªØ±Ø³Û Ø¨Ù ØªØµØ§ÙÛØ± Ø°Ø®ÛØ±Ù Ø´Ø¯Ù');
      });
    } else {
      this.imageInput.nativeElement.click();
    }
  }

  ngAfterViewInit(): void {
    const me = this;
    this.imageInput.nativeElement.addEventListener('change', (e) => {
      if (e.srcElement.files[0].size > me.maxImageSize * 1024) {
        const persianNumberPipe = new PersianNumberPipe();
        const message = persianNumberPipe.transform('Ø§ÙØ¯Ø§Ø²Ù ÙØ§ÛÙ ØªØµÙÛØ± ÙÙÛ ØªÙØ§ÙØ¯ Ø¨ÛØ´ Ø§Ø²' + ' ' + me.maxImageSize.toString() + ' ' + 'Ú©ÛÙÙ Ø¨Ø§ÛØª Ø¨Ø§Ø´Ø¯.');
        me.showAlert('Ù¾ÛØ§Ù Ø³ÛØ³ØªÙ', message);
        e.srcElement.value = '';
        return;
      }

      const reader = new FileReader();
      reader.readAsDataURL(e.srcElement.files[0]);
      reader.onload = (event) => {
        const model = new ImageModel();
        model.source = (<any>event.target).result;
        model.title = this.defaultTitle;
        model.tag = this.defaultTag;
        model.id = model.id = Guid.create().toString();
        if (!this.images.find(c => c.source === model.source)) {
          this.beforeInsert.emit();
          me.images.push(model);
          this.afterInsert.emit(model);
          this.changeDetectorRef.detectChanges();
          this._gallery.update();
          this.imageInput.nativeElement.value = '';
        }
      };
    });

    this._subscription.add(this.taminLazyLoadService.loadJs('assets/viewerjs/dist/viewer.js').subscribe(value => {
        this._subscription.add(
          this.taminLazyLoadService.loadCss('assets/viewerjs/dist/viewer.css').subscribe(value1 => {
            this._gallery = new Viewer(this.imageContainer.nativeElement);
          })
        );
      })
    );
  }

  addImage(model: ImageModel) {
    if (isNullOrUndefined(model)) {
      alert('Ø§Ø·ÙØ§Ø¹Ø§Øª ØªØµÙÛØ± ÙØ§Ø¯Ø±Ø³Øª Ø§Ø³Øª.');
      return;
    }
    this.beforeInsert.emit(model);
    model.id = Guid.create().toString();
    this.images.push(model);
    this.afterInsert.emit(model);
    this.changeDetectorRef.detectChanges();
    this._gallery.update();
  }

  removeImage(id: string) {
    this.showQuestionBox('Ù¾ÛØ§Ù Ø³ÛØ³ØªÙ', 'Ø¢ÛØ§ ÙØ·ÙØ¦Ù ÙØ³ØªÛØ¯', () => {
      const model = this.images.find(c => c.id === id);
      this.beforeRemove.emit(model);
      this.images = this.images.filter(obj => obj.id !== id);
      this.changeDetectorRef.detectChanges();
      this._gallery.update();
      this.afterRemove.emit(model);
    }, () => {
    });
  }

  startWaiting() {
    this._overlay = this.overlayService.show(this.host.nativeElement);
  }

  stopWaiting() {
    this.overlayService.hide(this._overlay);
  }

  ngOnDestroy(): void {
    this.stopWaiting();
    this._subscription.unsubscribe();
  }

  private showQuestionBox(title: string, message: string, yesCallback: Function, noCallback: Function) {
    alertify.confirm(title, message, yesCallback, noCallback)
      .set({
        labels: {ok: 'Ø¨ÙÛ', cancel: 'Ø®ÛØ±'}
      });
  }

  private showAlert(title: string, message: string) {
    alertify.alert(title, message);
  }

  getBase64FromImageUrl(url) {
    const me = this;
    const img = new Image();
    img.setAttribute('crossOrigin', 'anonymous');
    img.onload = () => {
      const canvas = document.createElement('canvas');
      canvas.width = img.width;
      canvas.height = img.height;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0);
      const dataURL = canvas.toDataURL('image/png');
      if (this.dataURLtoBlob(dataURL).size > me.maxImageSize * 1024) {
        const persianNumberPipe = new PersianNumberPipe();
        const message = persianNumberPipe.transform('Ø§ÙØ¯Ø§Ø²Ù ÙØ§ÛÙ ØªØµÙÛØ± ÙÙÛ ØªÙØ§ÙØ¯ Ø¨ÛØ´ Ø§Ø²' + ' ' + me.maxImageSize.toString() + ' ' + 'Ú©ÛÙÙ Ø¨Ø§ÛØª Ø¨Ø§Ø´Ø¯.');
        me.showAlert('Ù¾ÛØ§Ù Ø³ÛØ³ØªÙ', message);
        return;
      }
      const model = new ImageModel();
      model.source = dataURL;
      model.title = this.defaultTitle;
      model.tag = this.defaultTag;
      model.id = model.id = Guid.create().toString();
      if (!this.images.find(c => c.source === model.source)) {
        this.beforeInsert.emit();
        me.images.push(model);
        this.afterInsert.emit(model);
        this.changeDetectorRef.detectChanges();
        this._gallery.update();
        this.imageInput.nativeElement.value = '';
      }
      // alert(dataURL.replace(/^data:image\/(png|jpg);base64,/, ''));
    };
    img.src = url;
  }

  dataURLtoBlob(dataURL) {
    const binary = atob(dataURL.split(',')[1]);
    // Create 8-bit unsigned array
    const array = [];
    for (let i = 0; i < binary.length; i++) {
      array.push(binary.charCodeAt(i));
    }
    return new Blob([new Uint8Array(array)], {type: 'image/png'});
  }

  checkPermission(): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      // @ts-ignore
      const Permission = window.plugins.Permission;
      const permission = 'android.permission.WRITE_EXTERNAL_STORAGE';
      Permission.has(permission, (results) => {
        if (!results[permission]) {
          Permission.request(permission, (result) => {
            if (result[permission]) {
              resolve(true);
            } else {
              resolve(false);
            }
          }, (reason) => {
            reject(reason);
          });
        } else {
          resolve(true);
        }
      }, (reason) => {
        reject(reason);
      });
    });
  }
}

export class ImageModel {
  source: any;
  id: string;
  tag: any;
  title: string;
  removeable = true;
}
