import {AbstractControl, ValidatorFn} from '@angular/forms';
import * as momentNs from 'jalali-moment';

const moment = momentNs;


export class TaminValidators {

  static requiredNumber(control: AbstractControl): { [key: string]: any } | null {
    const value = control.value;
    if (value === undefined || value === null || value === '') {
      return null;
    }
    if (Number(value) !== 0) {
      return null;
    }
    return {'requiredNumber': false};
  }

  static nationalId(control: AbstractControl): { [key: string]: any } | null {
    if (control.value === undefined || control.value === null || control.value === '') {
      return undefined;
    }

    const value = control.value;

    if (value.length !== 10 && value.length !== 13) {
      return {'nationalId': false};
    }

    if (value.length === 10) {
      if (value === '0000000000' ||
        value === '1111111111' ||
        value === '2222222222' ||
        value === '3333333333' ||
        value === '4444444444' ||
        value === '5555555555' ||
        value === '6666666666' ||
        value === '7777777777' ||
        value === '8888888888' ||
        value === '9999999999'
      ) {
        return {'nationalId': false};
      }

      let sum = 0;

      for (let i = 0; i < 9; i++) {
        sum += value.charAt(i) * (10 - i);
      }

      const s = Math.floor(sum / 11);

      const a = sum - s * 11;

      const astr = a.toString();

      const c = value.charAt(9);

      const difa = (11 - a).toString();

      return ((a === 0 && astr === c) || (a === 1 && c === '1') || (a > 1 && c === difa)) ? null : {'nationalId': false};
    } else {
      return null;
    }
  }

  private static isNotPresent(control: AbstractControl): boolean {
    const value = control.value;
    if (value === undefined || value === null) {
      return true;
    }
    return value === '';
  }
}





