import {AfterContentInit, Component, ContentChildren, EventEmitter, OnInit, Output, QueryList} from '@angular/core';
import {TaminWizardItemComponent} from './tamin-wizard-item/tamin-wizard-item.component';

@Component({
  selector: 'tamin-wizard',
  templateUrl: './tamin-wizard.component.html',
  styleUrls: ['./tamin-wizard.component.scss']
})
export class TaminWizardComponent implements OnInit, AfterContentInit {
  @ContentChildren(TaminWizardItemComponent) items: QueryList<TaminWizardItemComponent>;
  private _current: number;
  @Output() stepChanging = new EventEmitter<WizardNavigationData>();
  @Output() stepChanged = new EventEmitter<WizardNavigationData>();

  constructor() {
  }

  get currentItem(): TaminWizardItemComponent {
    return this.items.toArray()[this._current];
  }

  get nextItem(): TaminWizardItemComponent {
    if (this._current < this.items.length) {
      return this.items.toArray()[this._current + 1];
    }
    return null;
  }

  get prevItem(): TaminWizardItemComponent {
    if (this._current > 0) {
      return this.items.toArray()[this._current - 1];
    }
    return null;
  }

  ngOnInit() {
  }

  ngAfterContentInit(): void {
    if (this.items.length > 0) {
      this._current = 0;
      this.refresh();
    }
  }


  onNextStep() {
    if (this.items.length > 0 && this.items.length > this._current) {
      if (this.stepChanging) {
        this.stepChanging.emit(new WizardNavigationData(this._current, 'forward'));
      } else {
        this.setNext();
        this.refresh();
        this.stepChanged.emit(new WizardNavigationData(this._current, 'forward'));
      }
    }
  }

  onPrevStep() {
    if (this.items.length > 0 && this._current > 0) {
      if (this.stepChanging) {
        this.stepChanging.emit(new WizardNavigationData(this._current, 'backward'));
      } else {
        this.setPrev();
        this.refresh();
        this.stepChanged.emit(new WizardNavigationData(this._current, 'backward'));
      }
    }
  }

  setNext() {
    if (this.items.length > 0 && this.items.length > this._current) {
      ++this._current;
    }
  }

  setPrev() {
    if (this.items.length > 0 && this._current > 0) {
      --this._current;
    }
  }

  setStep(step: number) {
    if (this.items.length > 0 && step > 0 && step < this.items.length) {
      this._current = step;
    }
  }

  public refresh() {
    this.items.forEach((item, index) => {
      if (item.visible) {
        item.hide();
      }
      if (this._current === index) {
        item.show();
      }
    });
  }
}


export class WizardNavigationData {
  constructor(currentStep: number, direction: 'forward' | 'backward') {
    this.currentStep = currentStep;
    this.direction = direction;
  }

  currentStep: number;
  direction: 'forward' | 'backward';
}
