import {Component, forwardRef} from '@angular/core';
import {FieldBaseComponent} from '../../../base/field-base.component';
import {NG_VALUE_ACCESSOR} from '@angular/forms';

@Component({
  selector: 'tamin-field-static-text',
  templateUrl: './tamin-field-static-text.component.html',
  styleUrls: ['./tamin-field-static-text.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldStaticTextComponent),
      multi: true
    }
  ]

})
export class TaminFieldStaticTextComponent extends FieldBaseComponent {
  initializeComponent() {
  }

  writeValue(value: any): void {
    setTimeout(() => {
      const theValue = value == null ? '' : value;
      this.inputElement.nativeElement.value = this.persianNumberPipe.transform(value);
      this.onChange(value);
    }, 0);
  }
}
