import {AbstractControl, ControlContainer, ControlValueAccessor, FormBuilder} from '@angular/forms';
import {AfterViewInit, Component, ElementRef, Injector, Input, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {fromEvent, Subscription} from 'rxjs';
import {debounceTime, distinctUntilChanged, timeout} from 'rxjs/operators';
import {OverlayService} from '../services/overlay/overlay.service';
import {PersianNumberPipe} from '../pipes/persian-number.pipe';
import {TaminRestService} from '../services/tamin-rest-service/tamin-rest.service';

declare let IMask: any;

@Component({
  selector: 'tamin-base-component',
  template: ''
})
export class FieldBaseComponent implements OnInit, ControlValueAccessor, OnDestroy, AfterViewInit {
  @ViewChild('inputElement') inputElement: ElementRef;
  @Input() public formControlName: string;
  @Input() public placeHolder: string;
  @Input() hint: string;
  control: AbstractControl;
  protected maskRef: any;
  protected subscription = new Subscription();
  protected onTouched: any;
  protected onChange: any;
  protected overlay: any;

  constructor(
    protected overlayService: OverlayService,
    protected restService: TaminRestService,
    protected controlContainer: ControlContainer,
    protected persianNumberPipe: PersianNumberPipe,
    protected injector: Injector, protected formBuilder: FormBuilder) {
    this.onTouched = () => {
    };
    this.onChange = (value: string) => {
    };
  }

  getInputElementRef(): ElementRef {
    return this.inputElement;
  }

  ngOnInit() {
    this.control = this.controlContainer.control.get(this.formControlName);
    // this.initValueAccessor();
    // this.initializeComponent();
  }

  ngAfterViewInit(): void {
    this.initValueAccessor();
    this.initializeComponent();
  }

  registerOnChange(fn: (_: any) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  writeValue(value: any): void {
    const theValue = value == null ? '' : value.toString();
    setTimeout(() => {
      if (this.maskRef) {
        this.maskRef.value = theValue;
      } else {
        this.inputElement.nativeElement.value = theValue;
      }
    });

    this.onChange(value);
  }

  ngOnDestroy(): void {
    if (this.overlay) {
      this.overlayService.hide(this.overlay);
    }
    this.subscription.unsubscribe();
  }

  protected getValue(val) {
    return val;
  }

  protected initValueAccessor() {
    const keyups = fromEvent(this.inputElement.nativeElement, 'keyup').pipe(debounceTime(100), distinctUntilChanged());
    this.subscription.add(keyups.subscribe((evt: any) => {
      if (this.maskRef) {
        this.onChange(this.getValue(this.maskRef.unmaskedValue));
      } else {
        this.onChange(this.getValue(evt.target.value));
      }
    }));

    const focuses = fromEvent(this.inputElement.nativeElement, 'focus');
    this.subscription.add(focuses.subscribe((evt: any) => {
        this.onTouched();
      }
    ));
  }

  startWaiting() {
    this.overlay = this.overlayService.show(this.inputElement.nativeElement);
  }

  stopWaiting() {
    this.overlayService.hide(this.overlay);
  }

  protected initializeComponent() {
  }

  protected deepFind(obj, path) {
    const paths = path.split('.');
    let current = obj;
    let i;

    for (i = 0; i < paths.length; ++i) {
      if (current[paths[i]] === undefined) {
        return undefined;
      } else {
        current = current[paths[i]];
      }
    }
    return current;
  }

}
