import {ChangeDetectorRef, Component, Input, OnInit, ViewChild} from '@angular/core';
import {PdfViewerComponent} from './pdf-viewer/pdf-viewer.component';
import {ChangeDetection} from '@angular/cli/lib/config/schema';
import {ImageViewerComponent} from './image-viewer/image-viewer.component';

@Component({
  selector: 'tamin-document-viewer',
  templateUrl: './tamin-document-viewer.component.html',
  styleUrls: ['./tamin-document-viewer.component.scss']
})
export class TaminDocumentViewerComponent implements OnInit {
  @ViewChild('pdfViewer') pdfViewer: PdfViewerComponent;
  @ViewChild('imageViewer') imageViewer: ImageViewerComponent;
  @Input() width = '100%';
  @Input() height = '600px';
  @Input() pdfJsPath = 'assets/pdfjs';

  viewerType: 'pdf' | 'image';

  constructor(private changeDetectorRef: ChangeDetectorRef) {
  }

  ngOnInit() {
  }

  loadPdf(url: string) {
    this.viewerType = 'pdf';
    this.changeDetectorRef.detectChanges();
    this.pdfViewer.open(url);
  }

  loadPdfBased64(url: string) {
    this.viewerType = 'pdf';
    this.changeDetectorRef.detectChanges();
    this.pdfViewer.openBased64(url);
  }

  loadImage(url: string) {
    this.viewerType = 'image';
    this.changeDetectorRef.detectChanges();
    this.imageViewer.open(url);
  }
}
