import {Component, ElementRef, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, ViewChild} from '@angular/core';
import {TaminLazyLoadService} from '../../services/tamin-lazy-load/tamin-lazy-load.service';
import { HostListener } from '@angular/core';

declare let Chart: any;

@Component({
  selector: 'tamin-chart',
  templateUrl: './tamin-chart.component.html',
  styleUrls: ['./tamin-chart.component.scss']
})
export class TaminChartComponent implements OnInit, OnDestroy, OnChanges {
  defaultColors: Array<number[]> = [
    [255, 99, 132],
    [54, 162, 235],
    [255, 206, 86],
    [231, 233, 237],
    [75, 192, 192],
    [151, 187, 205],
    [220, 220, 220],
    [247, 70, 74],
    [70, 191, 189],
    [253, 180, 92],
    [148, 159, 177],
    [77, 83, 96]
  ];

  @Input() public data: number[] | any[];
  @Input() public datasets: any[];
  @Input() public labels: Array<any> = [];
  @Input() public options: any = {};
  @Input() public chartType: string;
  @Input() public colors: Array<any>;
  @Input() public lib: string;
  @Input() public legend: boolean;
  @Output() public chartClick: EventEmitter<any> = new EventEmitter();
  @Output() public chartHover: EventEmitter<any> = new EventEmitter();
  @ViewChild('element') element: ElementRef;

  public ctx: any;
  public chart: any;
  private cvs: any;
  private initFlag = false;

  public constructor(private taminLazyLoadService: TaminLazyLoadService) {
  }

  public ngOnInit() {
    this.ctx = this.element.nativeElement.getContext('2d');
    this.cvs = this.element.nativeElement;
    this.initFlag = true;
    this.taminLazyLoadService.loadJs(this.lib).toPromise().then(() => {
      if (this.data || this.datasets) {
        this.refresh();
      }
    }).catch(error => {
      //console.error(error);
    });
  }

  public ngOnChanges(changes: SimpleChanges): void {
    if (this.initFlag) {
      // Check if the changes are in the data or datasets
      if (changes.hasOwnProperty('data') || changes.hasOwnProperty('datasets')) {
        if (changes['data']) {
          this.updateChartData(changes['data'].currentValue);
        } else {
          this.updateChartData(changes['datasets'].currentValue);
        }

        this.chart.update();
      } else {
        // otherwise rebuild the chart
        this.refresh();
      }
    }
  }

  public ngOnDestroy() {
    if (this.chart) {
      this.chart.destroy();
      this.chart = void 0;
    }
  }

  public getChartBuilder(ctx: any/*, data:Array<any>, options:any*/): any {
    const datasets: any = this.getDatasets();

    const options: any = Object.assign({}, this.options);
    if (this.legend === false) {
      options.legend = {display: false};
    }
    // hock for onHover and onClick events
    options.hover = options.hover || {};
    if (!options.hover.onHover) {
      options.hover.onHover = (active: Array<any>) => {
        if (active && !active.length) {
          return;
        }
        this.chartHover.emit({active});
      };
    }

    if (!options.onClick) {
      options.onClick = (event: any, active: Array<any>) => {
        this.chartClick.emit({event, active});
      };
    }

    const opts = {
      type: this.chartType,
      data: {
        labels: this.labels,
        datasets: datasets
      },
      options: options
    };

    return new Chart(ctx, opts);
  }

  private updateChartData(newDataValues: number[] | any[]): void {
    if (Array.isArray(newDataValues[0].data)) {
      this.chart.data.datasets.forEach((dataset: any, i: number) => {
        dataset.data = newDataValues[i].data;

        if (newDataValues[i].label) {
          dataset.label = newDataValues[i].label;
        }
      });
    } else {
      this.chart.data.datasets[0].data = newDataValues;
    }
  }

  private getDatasets(): any {
    let datasets: any = void 0;
    // in case if datasets is not provided, but data is present
    if (!this.datasets || !this.datasets.length && (this.data && this.data.length)) {
      if (Array.isArray(this.data[0])) {
        datasets = (this.data as Array<number[]>).map((data: number[], index: number) => {
          return {data, label: this.labels[index] || `Label ${index}`};
        });
      } else {
        datasets = [{data: this.data, label: `Label 0`}];
      }
    }

    if (this.datasets && this.datasets.length ||
      (datasets && datasets.length)) {
      datasets = (this.datasets || datasets)
        .map((elm: number, index: number) => {
          const newElm: any = Object.assign({}, elm);
          if (this.colors && this.colors.length) {
            Object.assign(newElm, this.colors[index]);
          } else {
            Object.assign(newElm, this.getColors(this.chartType, index, newElm.data.length));
          }
          return newElm;
        });
    }

    if (!datasets) {
      throw new Error(`ng-charts configuration error, data or datasets field are required to render char ${this.chartType}`);
    }

    return datasets;
  }

  private refresh(): any {
    this.chart = this.getChartBuilder(this.ctx);
  }

  private rgba(colour: Array<number>, alpha: number): string {
    return 'rgba(' + colour.concat(alpha).join(',') + ')';
  }

  private getRandomInt(min: number, max: number): number {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  private formatLineColor(colors: Array<number>): Color {
    return {
      backgroundColor: this.rgba(colors, 0.4),
      borderColor: this.rgba(colors, 1),
      pointBackgroundColor: this.rgba(colors, 1),
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: this.rgba(colors, 0.8)
    };
  }

  private formatBarColor(colors: Array<number>): Color {
    return {
      backgroundColor: this.rgba(colors, 0.6),
      borderColor: this.rgba(colors, 1),
      hoverBackgroundColor: this.rgba(colors, 0.8),
      hoverBorderColor: this.rgba(colors, 1)
    };
  }

  private formatPieColors(colors: Array<number[]>): Colors {
    return {
      backgroundColor: colors.map((color: number[]) => this.rgba(color, 0.6)),
      borderColor: colors.map(() => '#fff'),
      pointBackgroundColor: colors.map((color: number[]) => this.rgba(color, 1)),
      pointBorderColor: colors.map(() => '#fff'),
      pointHoverBackgroundColor: colors.map((color: number[]) => this.rgba(color, 1)),
      pointHoverBorderColor: colors.map((color: number[]) => this.rgba(color, 1))
    };
  }

  private formatPolarAreaColors(colors: Array<number[]>): Color {
    return {
      backgroundColor: colors.map((color: number[]) => this.rgba(color, 0.6)),
      borderColor: colors.map((color: number[]) => this.rgba(color, 1)),
      hoverBackgroundColor: colors.map((color: number[]) => this.rgba(color, 0.8)),
      hoverBorderColor: colors.map((color: number[]) => this.rgba(color, 1))
    };
  }

  private getRandomColor(): number[] {
    return [this.getRandomInt(0, 255), this.getRandomInt(0, 255), this.getRandomInt(0, 255)];
  }

  private generateColor(index: number): number[] {
    return this.defaultColors[index] || this.getRandomColor();
  }

  private generateColors(count: number): Array<number[]> {
    const colorsArr: Array<number[]> = new Array(count);
    for (let i = 0; i < count; i++) {
      colorsArr[i] = this.defaultColors[i] || this.getRandomColor();
    }
    return colorsArr;
  }

  private getColors(chartType: string, index: number, count: number): Color {
    if (chartType === 'pie' || chartType === 'doughnut') {
      return this.formatPieColors(this.generateColors(count));
    }

    if (chartType === 'polarArea') {
      return this.formatPolarAreaColors(this.generateColors(count));
    }

    if (chartType === 'line' || chartType === 'radar') {
      return this.formatLineColor(this.generateColor(index));
    }

    if (chartType === 'bar' || chartType === 'horizontalBar') {
      return this.formatBarColor(this.generateColor(index));
    }
    return this.generateColor(index) as Color;
  }


  @HostListener('window:resize', ['$event'])
  windowResizeHandler(event) {
    //this.refresh(); Makes some problems on hover.
  }

}

export interface Color {
  backgroundColor?: string | string[];
  borderWidth?: number | number[];
  borderColor?: string | string[];
  borderCapStyle?: string;
  borderDash?: number[];
  borderDashOffset?: number;
  borderJoinStyle?: string;

  pointBorderColor?: string | string[];
  pointBackgroundColor?: string | string[];
  pointBorderWidth?: number | number[];

  pointRadius?: number | number[];
  pointHoverRadius?: number | number[];
  pointHitRadius?: number | number[];

  pointHoverBackgroundColor?: string | string[];
  pointHoverBorderColor?: string | string[];
  pointHoverBorderWidth?: number | number[];
  pointStyle?: string | string[];

  hoverBackgroundColor?: string | string[];
  hoverBorderColor?: string | string[];
  hoverBorderWidth?: number;
}

export interface Colors extends Color {
  data?: number[];
  label?: string;
}



