import {Component, EventEmitter, forwardRef, Input, Output} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {FieldBaseComponent} from '../../../base/field-base.component';


@Component({
  selector: 'tamin-field-text',
  templateUrl: './tamin-field-text.component.html',
  styleUrls: ['./tamin-field-text.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldTextComponent),
      multi: true
    }
  ]
})
export class TaminFieldTextComponent extends FieldBaseComponent {

  @Input() maxLength = Number.MAX_SAFE_INTEGER;
  @Output() focus = new EventEmitter();
  @Output() blur = new EventEmitter();

}
