import {Inject, Injectable} from '@angular/core';
import {ITaminApplicationConfig} from '../interfaces/tamin-application-config';

@Injectable({
  providedIn: 'root'
})
export class RequestCacheService {

  private cache = new Map();

  constructor(@Inject('taminApplicationConfig') private taminApplicationConfig: ITaminApplicationConfig) {
  }


  isCacheable(url: string): boolean {
    const tmp = this.taminApplicationConfig.cacheableUrls.find(c => c === url);
    return tmp !== undefined;

  }

  hasCached(url: string): boolean {
    return this.cache.has(url);

  }

  getCached(url: string) {
    return this.cache.get(url);
  }

  setCache(url: string, data: any) {
    return this.cache.set(url, data);
  }

  deleteCache(url: string) {
    this.cache.delete(url);
  }
}
