import {AfterContentInit, Component, ContentChildren, Input, OnInit, QueryList} from '@angular/core';
import {TaminTabComponent} from '../tamin-tab/tamin-tab.component';

@Component({
  selector: 'tamin-tab-panel',
  templateUrl: './tamin-tab-panel.component.html',
  styleUrls: ['./tamin-tab-panel.component.scss']
})
export class TaminTabPanelComponent implements OnInit, AfterContentInit {
  @ContentChildren(TaminTabComponent) tabComponents: QueryList<TaminTabComponent>;
  @Input() currentTab = 0;

  constructor() {
  }

  ngOnInit() {
  }

  ngAfterContentInit() {
    console.log('ngAfterContentInit');
    this.tabComponents.forEach((item, index) => {
      if (index === this.currentTab) {
        item.present = true;
      }
    });
  }

  onClick(tab, index) {
    this.currentTab = index;
    this.tabComponents.forEach(item => item.present = item === tab);
  }
}
