import {Component, OnInit, ViewChild} from '@angular/core';
import {TaminDocumentViewerComponent} from '../tamin-document-viewer.component';
import {TaminModalComponent} from '../../tamin-modal/tamin-modal.component';

@Component({
  selector: 'tamin-document-viewer-modal',
  templateUrl: './tamin-document-viewer-modal.component.html',
  styleUrls: ['./tamin-document-viewer-modal.component.css']
})
export class TaminDocumentViewerModalComponent implements OnInit {

  @ViewChild('documentViewer') documentViewer: TaminDocumentViewerComponent;
  @ViewChild('theModal') theModal: TaminModalComponent;

  constructor() { }

  ngOnInit() {
  }

  openImage(url) {
    this.theModal.show();
    this.documentViewer.loadImage(url);
  }

  openPdf(url) {
    this.theModal.show();
    this.documentViewer.loadPdf(url);
  }
  openPdfBased64(url) {
    this.theModal.show();
    this.documentViewer.loadPdfBased64(url);
  }
}
