import {Component, forwardRef, Input} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {FieldBaseComponent} from '../../../base/field-base.component';

declare let IMask: any;

@Component({
  selector: 'tamin-field-mask',
  templateUrl: './tamin-field-mask.component.html',
  styleUrls: ['./tamin-field-mask.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldMaskComponent),
      multi: true
    }
  ]
})
export class TaminFieldMaskComponent extends FieldBaseComponent {
  @Input() set mask(mask: string) {
    const me = this;
    setTimeout(() => {
      this.maskRef = new IMask(this.inputElement.nativeElement, {
        mask: mask,
      });
    }, 0);
  }

  get mask(): string {
    return this.maskRef.mask;
  }

  @Input() set regex(regex: string) {
    const me = this;

    setTimeout(() => {
      this.maskRef = new IMask(this.inputElement.nativeElement, {
        mask: new RegExp(regex),
      });
    }, 0);
  }

  get regex(): string {
    return this.maskRef.regex;
  }

  initializeComponent() {
  }
}
