import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'tamin-menu-button',
  templateUrl: './tamin-menu-button.component.html',
  styleUrls: ['./tamin-menu-button.component.scss']
})
export class TaminMenuButtonComponent implements OnInit {
  menuId: string;
  @Input() menuTitle: string;
  checkable: 'none';
  menuItems: Array<MenuItem> = [];
  expanded = 'false';

  constructor() {
  }

  ngOnInit() {
    this.menuId = 'id_' + new Date().getTime().toString();
  }

  onMenuClicked(name: string) {

  }

  onButtonClicked() {
    this.expanded = this.expanded === 'false' ? 'true' : 'false';
  }
}


export class MenuItem {
  title: string;
  disabled: boolean;
  name: string;

  constructor(title: string, disabled: boolean, name: string) {
    this.title = title;
    this.disabled = disabled;
    this.name = name;
  }
}
