import {Component, forwardRef, Input, OnInit} from '@angular/core';
import {AbstractControl, ControlContainer, ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';

@Component({
  selector: 'tamin-field-label',
  templateUrl: './tamin-field-label.component.html',
  styleUrls: ['./tamin-field-label.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldLabelComponent),
      multi: true
    }
  ]
})
export class TaminFieldLabelComponent implements OnInit, ControlValueAccessor {

  @Input() formControlName: string;
  control: AbstractControl;

  constructor(private controlContainer: ControlContainer) {
  }

  ngOnInit() {
    this.control = this.controlContainer.control.get(this.formControlName);
  }

  get required(): boolean {
    if (this.control.validator === null) {
      return false;
    }
    const validator = this.control.validator({} as AbstractControl);
    return (validator && (validator.required || validator.requiredNumber));
  }


  registerOnChange(fn: any): void {
  }

  registerOnTouched(fn: any): void {
  }

  setDisabledState(isDisabled: boolean): void {
  }

  writeValue(obj: any): void {
  }

}
