import {Component, EventEmitter, forwardRef, Input, OnInit, Output} from '@angular/core';
import {FieldBaseComponent} from '../../../base/field-base.component';
import {NG_VALUE_ACCESSOR} from '@angular/forms';

declare let IMask: any;

@Component({
  selector: 'tamin-tamin-field-calculable',
  templateUrl: './tamin-field-calculable.component.html',
  styleUrls: ['./tamin-field-calculable.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldCalculableComponent),
      multi: true
    }
  ]
})
export class TaminFieldCalculableComponent extends FieldBaseComponent {

  @Input() min = Number.MIN_SAFE_INTEGER;
  @Input() max = Number.MAX_SAFE_INTEGER;
  @Input() negativeNumberColor = 'red';
  @Output('calculate') calculate = new EventEmitter();

  initializeComponent() {
    const me = this;
    this.maskRef = new IMask(this.inputElement.nativeElement, {
      mask: Number,
      thousandsSeparator: ',',
      min: me.min,
      max: me.max
    });
  }

  writeValue(value: any): void {
    const theValue = value == null ? '' : value;
    setTimeout(() => {
      if (this.maskRef) {
        this.maskRef.value = value.toString();
      } else {
        this.inputElement.nativeElement.value = value.toString();
      }
    });

    this.onChange(value);
  }


  onCalculate() {
    this.calculate.emit();
  }
}
