import {
  AfterContentChecked,
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Injector,
  Input,
  OnDestroy,
  OnInit
} from '@angular/core';
import {OverlayService} from '../services/overlay/overlay.service';
import {TaminRestService} from '../services/tamin-rest-service/tamin-rest.service';
import {TaminSecurityService} from '../services/tamin-security/tamin-security.service';
import {PersianDateTimePipe} from '../pipes/persian-date-time.pipe';
import {PersianNumberPipe} from '../pipes/persian-number.pipe';
import {Router} from '@angular/router';
import {FormBuilder, FormGroup} from '@angular/forms';
import {ConstantsService} from '../services/constants.service';

declare var alertify: any;

declare let introJs: any;

@Component({
  selector: 'tamin-page-base-component',
  template: '',
})
export class TaminPageBaseComponent implements OnInit, OnDestroy, AfterViewInit, AfterContentChecked {
  private _title: string;
  private overlays = [];
  private _overlayService: OverlayService;
  private _restService: TaminRestService;
  private _securityService: TaminSecurityService;
  private _persianDateTime: PersianDateTimePipe;
  private _persianNumberPipe: PersianNumberPipe;
  private _router: Router;
  private _constants: ConstantsService;
  private _formBuilder: FormBuilder;
  private _host: ElementRef;
  private _changeDetectorRef: ChangeDetectorRef;
  @Input() isModal = false;

  @Input() set visible(val: boolean) {
    this._host.nativeElement.style.display = val ? 'block' : 'none';
  }

  constructor(private injector: Injector) {
    this._overlayService = injector.get(OverlayService);
    this._restService = injector.get(TaminRestService);
    this._securityService = injector.get(TaminSecurityService);
    this._persianDateTime = injector.get(PersianDateTimePipe);
    this._persianNumberPipe = injector.get(PersianNumberPipe);
    this._router = injector.get(Router);
    this._formBuilder = injector.get(FormBuilder);
    this._host = injector.get(ElementRef);
    this._changeDetectorRef = injector.get(ChangeDetectorRef);
    this._constants = injector.get(ConstantsService);
  }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.internalInitialize();
    this.initializePage();
  }

  set title(val: string) {
    this._title = val;
    document.title = 'Ø³Ø§Ø²ÙØ§Ù ØªØ§ÙÛÙ Ø§Ø¬ØªÙØ§Ø¹Û' + ' - ' + this._title;
  }

  get title(): string {
    return this._title;
  }

  ngOnDestroy(): void {
    this.overlays.forEach((item: any) => {
      this._overlayService.hide(item.overlay);
    });
    this.destroyPage();
    alertify.closeAll();
  }

  get restService(): TaminRestService {
    return this._restService;
  }

  get constants(): ConstantsService {
    return this._constants;
  }

  get formBuilder(): FormBuilder {
    return this._formBuilder;
  }

  // get currentUser(): any {
  //   return this._securityService.currentUser;
  // }

  get changeDetectorRef(): ChangeDetectorRef {
    return this._changeDetectorRef;
  }

  get securityService(): TaminSecurityService {
    return this._securityService;
  }

  getPersianDate(date: any): string {
    try {
      return this._persianDateTime.transform(new Date(date));
    } catch {
      return 'ØªØ§Ø±ÛØ® ÙØ§Ø¯Ø±Ø³Øª';
    }
  }

  redirectTo(path) {
    this._router.navigate([path]);
  }

  showInfoMessageBox(title: string, message: string, callback: Function = null) {
    if (callback) {
      alertify.alert(title, message, callback);
    } else {
      alertify.alert(title, message);
    }
  }

  showErrorMessageBox(title: string, message: string, callback: Function = null) {
    const theTitle = `<span style="color: red">${title}</span>`;
    if (callback) {
      alertify.alert(theTitle, message, callback);
    } else {
      alertify.alert(theTitle, message);
    }
  }

  showQuestionBox(title: string, message: string, yesCallback: Function, noCallback: Function) {
    alertify.confirm(title, message, yesCallback, noCallback)
      .set({
        labels: {ok: 'Ø¨ÙÛ', cancel: 'Ø®ÛØ±'}
      });
  }

  showRetryBox(title: string, message: string, retryCallback: Function, cancelCallback: Function) {
    alertify.confirm(title, message, retryCallback, cancelCallback)
      .set({
        labels: {ok: 'ØªÙØ§Ø´ ÙØ¬Ø¯Ø¯', cancel: 'Ø§ÙØµØ±Ø§Ù'},
        'closable': false
      });
  }

  getPersianNumber(str: string): string {
    return this._persianNumberPipe.transform(str);
  }

  protected initializePage() {
  }

  protected destroyPage() {
  }

  showOverlay(element: any = null): string {
    if (this.overlays.findIndex(c => c.overlay === element) >= 0) {
      return;
    }
    const overlay = this._overlayService.show(element);
    const id = (new Date()).getTime();
    this.overlays.push({
      overlay: overlay,
      id: id.toString()
    });
    return id.toString();
  }

  showOverlayWithMessage(message: string, element: any = null): string {
    if (this.overlays.findIndex(c => c.overlay === element) >= 0) {
      return;
    }
    const overlay = this._overlayService.showMessage(message, element);
    const id = (new Date()).getTime();
    this.overlays.push({
      overlay: overlay,
      id: id.toString()
    });
    return id.toString();
  }

  hideOverlay(id: string) {
    const tmp = this.overlays.find(c => c.id === id);
    if (tmp) {
      this._overlayService.hide(tmp.overlay);
    }
    const index = this.overlays.findIndex(c => c.id === id);
    this.overlays.splice(index, 1);
  }

  private internalInitialize() {
  }

  protected loadPageData() {
  }

  onTour() {
    introJs().setOptions(
      {
        'nextLabel': 'Ø¨Ø¹Ø¯',
        'prevLabel': 'ÙØ¨Ù',
        'skipLabel': 'Ø®Ø±ÙØ¬',
        'doneLabel': 'Ø§ØªÙØ§Ù'
      }).start();
  }

  ngAfterViewInit(): void {
    setTimeout(() => this.loadPageData(), 0);
  }

  openModal(): any {
  }

  markFormGroupAsTouched(form: FormGroup) {
    (<any>Object).values(form.controls).forEach(control => control.markAsTouched());
  }

  ngAfterContentChecked(): void {
    this._changeDetectorRef.detectChanges();
  }
}
