import {Injectable} from '@angular/core';
import {CookieService} from 'ngx-cookie-service';
import {DeviceDetectorService} from 'ngx-device-detector';

@Injectable({
  providedIn: 'root'
})
export class TaminStorageService {

  private policy: 'local-storage' | 'cookie' | 'memory';
  private memoryStorage = [];

  constructor(private cookieService: CookieService, private deviceService: DeviceDetectorService) {
    if (deviceService.os === 'iPhone' || deviceService.os === 'iPad' || deviceService.browser === 'safari' || deviceService.browser === 'Safari') {
      this.policy = 'cookie';
      return;
    }
    if (this.doLocalStorageWork()) {
      this.policy = 'local-storage';
    } else if (this.doCookieWork()) {
      this.policy = 'cookie';
    } else {
      this.policy = 'memory';
    }
  }

  private doLocalStorageWork(): boolean {
    try {
      localStorage.setItem('dummy', 'dummy');
      if (localStorage.getItem('dummy') === 'dummy') {
        localStorage.removeItem('dummy');
        return true;
      } else {
        localStorage.removeItem('dummy');
        return false;
      }
    } catch {
      return false;
    }
  }

  private doCookieWork(): boolean {
    try {
      this.cookieService.set('dummy', 'dummy', null, null, null, true, 'Strict');
      if (this.cookieService.get('dummy') === 'dummy') {
        this.cookieService.delete('dummy');
        return true;
      } else {
        this.cookieService.delete('dummy');
        return false;
      }
    } catch {
      return false;
    }
  }

  set(key: string, value: string) {
    switch (this.policy) {
      case 'local-storage':
        localStorage.setItem(key, value);
        break;
      case 'cookie':
        this.cookieService.set(key, value, null, null, null, null, 'Strict');
        break;
      case 'memory':
        const item = this.memoryStorage.find(c => c.name === key);
        if (item) {
          item.value = value;
        } else {
          this.memoryStorage.push({name: key, value: value});
        }
        break;
    }
  }

  get(key: string): string {
    switch (this.policy) {
      case 'local-storage':
        return localStorage.getItem(key);
      case 'cookie':
        return this.cookieService.get(key);
      case 'memory':
        const item = this.memoryStorage.find(c => c.name === key);
        if (item) {
          return item.value;
        } else {
          return '';
        }
    }
  }

  exists(key: string): boolean {
    switch (this.policy) {
      case 'local-storage':
        return localStorage.getItem(key) !== null;
      case 'cookie':
        return this.cookieService.get(key) !== '';
      case 'memory':
        const item = this.memoryStorage.find(c => c.name === key);
        return item !== null;
    }
  }

  remove(key: string) {
    switch (this.policy) {
      case 'local-storage':
        localStorage.removeItem(key);
        break;
      case 'cookie':
        this.cookieService.delete(key);
        break;
      case 'memory':
        const index = this.memoryStorage.findIndex(c => c.name === key);
        if (index >= 0) {
          delete this.memoryStorage[index];
        }
    }
  }
}



