import {Component, forwardRef, Input, OnInit} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {FieldBaseComponent} from '../../../base/field-base.component';
declare let IMask: any;

@Component({
  selector: 'tamin-field-persian-text',
  templateUrl: './tamin-field-persian-text.component.html',
  styleUrls: ['./tamin-field-persian-text.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TaminFieldPersianTextComponent),
      multi: true
    }
  ]

})
export class TaminFieldPersianTextComponent  extends FieldBaseComponent {

  @Input() maxLength = Number.MAX_SAFE_INTEGER;
  initializeComponent() {
    this.maskRef = new IMask(this.inputElement.nativeElement, {
      mask: /^[\u0600-\u06FF\s]+$/,
    });
  }


}
