import {Component, ContentChildren, Input, OnInit, QueryList} from '@angular/core';
import {TaminStepperStepComponent} from './tamin-stepper-step/tamin-stepper-step.component';
import {BreakpointObserver, Breakpoints, BreakpointState} from '@angular/cdk/layout';

@Component({
  selector: 'tamin-tamin-stepper',
  templateUrl: './tamin-stepper.component.html',
  styleUrls: ['./tamin-stepper.component.scss']
})
export class TaminStepperComponent implements OnInit {

  @ContentChildren(TaminStepperStepComponent)
  steps: QueryList<TaminStepperStepComponent>;
  private _activeStepName: string;
  breakPoints: Array<any> = [Breakpoints.Small, Breakpoints.Handset];
  renderMode: 'desktop' | 'mobile' = 'desktop';

  @Input() set activeStepName(value) {
    this._activeStepName = value;
    setTimeout(() => {
      this.steps.forEach((item, index) => {
        item.active = item.name === value;
      });
    });
  }

  get activeStepName(): string {
    return this._activeStepName;
  }

  constructor(public breakpointObserver: BreakpointObserver) {
  }

  ngOnInit() {
    this.breakpointObserver
      .observe(this.breakPoints)
      .subscribe((state: BreakpointState) => {
        if (state.matches) {

          if (this.renderMode !== 'mobile') {
            this.renderMode = 'mobile';
          }
        } else {
          if (this.renderMode !== 'desktop') {
            this.renderMode = 'desktop';
          }
        }
      });

    setTimeout(() => {
      this.steps.forEach((item, index) => {
        item.iconText = (index + 1).toString();
      });
    });
  }
}
