import {
  AfterContentChecked,
  AfterContentInit, AfterViewInit,
  Component,
  ContentChildren, ElementRef, HostListener,
  Input,
  OnInit,
  QueryList,
  ViewChild
} from '@angular/core';
import {TaminTabItemComponent} from '../tab-item/tab-item.component';
import {Observable} from 'rxjs';
import {delay, map, startWith} from 'rxjs/operators';

@Component({
  selector: 'tamin-tabs',
  templateUrl: './tabs.component.html',
  styleUrls: ['./tabs.component.scss']
})
export class TaminTabsComponent implements OnInit, AfterContentInit, AfterContentChecked, AfterViewInit {
  @ContentChildren(TaminTabItemComponent)
  tabs: QueryList<TaminTabItemComponent>;
  tabItems: Observable<TaminTabItemComponent[]>;
  activeTab: TaminTabItemComponent;
  layout: 'vertical' | 'horizontal' = 'horizontal';
  @Input() tabPosition: 'top' | 'bottom' = 'top';
  @ViewChild('tabHeader') tabHeader: ElementRef;
  scroll: boolean;

  ngOnInit(): void {
  }

  ngAfterContentInit(): void {
    this.tabItems = this.tabs.changes
      .pipe(startWith(''))
      .pipe(delay(0))
      .pipe(map(() => this.tabs.toArray()));
  }

  ngAfterContentChecked() {
    if (!this.activeTab) {
      Promise.resolve().then(() => {
        this.activeTab = this.tabs.first;
      });
    }
  }

  selectTab(tabItem: TaminTabItemComponent) {
    if (this.activeTab === tabItem) {
      return;
    }

    if (this.activeTab) {
      this.activeTab.isActive = false;
    }

    this.activeTab = tabItem;

    tabItem.isActive = true;
  }


  getBorderClass() {
    return this.tabPosition === 'top' ? 'border-bottom' : 'border-top';
  }

  @HostListener('window:resize')
  onResize() {
    const hasScroll = this.hasScroll(this.tabHeader.nativeElement, 'horizontal');
    this.scroll = hasScroll;
  }

  private hasScroll(el, direction) {
    direction = (direction === 'vertical') ? 'scrollTop' : 'scrollLeft';
    let result = !!el[direction];

    if (!result) {
      el[direction] = 1;
      result = !!el[direction];
      el[direction] = 0;
    }
    return result;
  }

  scrollTo(direction: 'left' | 'right') {
    this.tabHeader.nativeElement.scrollLeft += direction === 'left' ? -100 : 100;
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.onResize();
    });
  }
}
