import {Component, ElementRef, Input, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {TaminLazyLoadService} from '../../../services/tamin-lazy-load/tamin-lazy-load.service';
import {Subscription} from 'rxjs';
import {DomSanitizer} from '@angular/platform-browser';

declare let PDFViewerApplication: any;

@Component({
  selector: 'tamin-pdf-viewer2',
  templateUrl: './pdf-viewer.component.html',
  styleUrls: ['./pdf-viewer.component.scss']
})
export class PdfViewerComponent implements OnInit, OnDestroy {

  @ViewChild('iframe') iframe: ElementRef;

  @Input() width: string;
  @Input() height: string;

  private baseUrl = 'assets/pdfjs/viewer.html';
  // viewerUrl: any;

  private subscription = new Subscription();

  constructor(private taminLazyLoadService: TaminLazyLoadService, private domSanitizer: DomSanitizer) {

  }

  ngOnDestroy(): void {
  }

  ngOnInit(): void {
  }


  based64toBlob(b64Data, contentType, sliceSize = 512) {
    contentType = contentType || '';
    sliceSize = sliceSize || 512;
    const byteCharacters = atob(b64Data);
    const byteArrays = [];
    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);
      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }
      const byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }
    return new Blob(byteArrays, {type: contentType});
  }

  openBased64(data) {
    const tmp = URL.createObjectURL(this.based64toBlob(data, 'application/pdf'));
    this.open(tmp);
  }

  open(url: string): void {
    this.iframe.nativeElement.src = `${this.baseUrl}?file=${url}`;
  }
}
