import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {OverlayService} from '../../services/overlay/overlay.service';

declare let PlainModal: any;
declare let PlainDraggable: any;

declare var alertify: any;

@Component({
  selector: 'tamin-modal',
  templateUrl: './tamin-modal.component.html',
  styleUrls: ['./tamin-modal.component.scss']
})
export class TaminModalComponent implements OnInit {
  @ViewChild('modalElement') modalElement: ElementRef;
  @Input() title: string;
  @Input() width = '80%';
  @Input() maximizable: true;
  button1: any;
  button2: any;
  button3: any;
  modal: any;
  draggable: any;
  self: any;
  private _overlay = null;
  @Output() buttonClicked = new EventEmitter<any>();


  private alertifyRef = null;

  constructor(private overlayService: OverlayService) {
  }

  ngOnInit() {
    this.createDialog();
  }

  show() {
    if (this.alertifyRef) {
      this.alertifyRef.destroy();
      this.alertifyRef = null;
    }
    // self = this.modal.open();
    if (this.title === '') {
      this.title = ' ';
    }
    this.modalElement.nativeElement.style.display = 'block';
    this.alertifyRef = alertify.genericDialog(this.modalElement.nativeElement, this.title, this.width, this.maximizable);
  }

  showOverlay() {
    if (this.alertifyRef === null) {
      return;
    }
    if (this._overlay) {
      return;
    }

    this._overlay = this.overlayService.show(this.alertifyRef.elements.dialog);
  }

  hideOverlay() {
    if (this.alertifyRef === null) {
      return;
    }

    if (!this._overlay) {
      return;
    }

    this.overlayService.hide(this._overlay);
  }


  hide() {
    if (this.alertifyRef === null) {
      return;
    }
    this.alertifyRef.destroy();
    this.alertifyRef = null;
  }

  createDialog() {
    const me = this;
    if (!alertify.genericDialog) {
      const buttons = [];
      if (this.button1) {
        buttons.push(this.button1);
      }
      if (this.button2) {
        buttons.push(this.button2);
      }
      if (this.button3) {
        buttons.push(this.button3);
      }
      alertify.dialog('genericDialog', function () {
        return {
          main: function (content, title, width, maximizable) {
            this.setting('title', title);
            this.setting('width', width);
            this.setting('maximizable', maximizable);
            this.setContent(content);
          },
          setup: function () {
            return {
              buttons: buttons,
              options: {
                resizable: false,
                padding: false,
                frameless: false,
                autoReset: false,
                closableByDimmer: false
              }
            };
          },
          callback: function (closeEvent) {
            me.buttonClicked.emit(closeEvent);
          },
          settings: {
            width: '',
            onClose: undefined
          },
          hooks: {
            onrestore: function () {
              this.elements.dialog.style.width = this.get('width');
            },
            onshow: function () {
              this.elements.dialog.style.maxWidth = 'none';
              this.elements.dialog.style.width = this.get('width');
            }
          }
        };
      });
    }
  }
}
